"use client";

import * as React from "react";
import { zodResolver } from "@hookform/resolvers/zod";
import { useForm } from "react-hook-form";
import { z } from "zod";
import { Button } from "@/components/ui/button";
import {
  Form,
  FormControl,
  FormDescription,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from "@/components/ui/popover";
import { Calendar } from "@/components/ui/calendar";
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group";
import { cn } from "@/lib/utils";
import { format } from "date-fns";
import { Calendar as CalendarIcon } from "lucide-react";
import type { Lead, CallStatus, CallOutcome } from "@/types/lead";
import { callStatuses, callOutcomes } from './lead-table-columns'; // Import options

interface LeadEditFormProps {
  lead: Lead;
  userRole: "admin" | "employee";
  onSubmit: (data: Partial<Lead>) => Promise<void>; // Make onSubmit async
  onCancel: () => void;
  isLoading: boolean;
}

// Define Zod schema based on Lead type, making fields optional for partial updates
// We make most fields optional because we only submit changed fields.
// Required fields like `id` are handled separately.
const formSchema = z.object({
  name: z.string().optional(),
  company: z.string().optional(),
  email: z.string().email().optional().or(z.literal("")), // Allow empty string for optional email
  phone: z.string().optional(),
  source: z.string().optional(),
  assignedTo: z.string().optional(), // Employee ID or name
  location: z.string().optional(),
  remark: z.string().optional(),
  callDate: z.date().optional().nullable(),
  callBookedLost: z.enum(["Booked", "Lost"]).optional().nullable(),
  callStatus: z.enum(callStatuses.filter(s => s !== null) as [CallStatus, ...CallStatus[]]).optional().nullable(),
  callOutcome: z.enum(callOutcomes.filter(o => o !== null) as [CallOutcome, ...CallOutcome[]]).optional().nullable(),
  nadDate: z.date().optional().nullable(),
  nadBookedLost: z.enum(["Booked", "Lost"]).optional().nullable(),
});

type FormValues = z.infer<typeof formSchema>;


// Helper to check if a field is editable by the current user role
const isFieldEditable = (fieldName: keyof FormValues, role: "admin" | "employee"): boolean => {
  if (role === "admin") {
    return true; // Admin can edit all fields defined in the schema
  }
  // Employee editable fields
  const employeeEditableFields: (keyof FormValues)[] = [
    "callDate", "callBookedLost", "callStatus", "callOutcome",
    "nadDate", "nadBookedLost", "remark", "location"
  ];
  return employeeEditableFields.includes(fieldName);
};

export function LeadEditForm({ lead, userRole, onSubmit, onCancel, isLoading }: LeadEditFormProps) {

  // Initialize the form with lead data
  const form = useForm<FormValues>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      name: lead.name || "",
      company: lead.company || "",
      email: lead.email || "",
      phone: lead.phone || "",
      source: lead.source || "",
      assignedTo: lead.assignedTo || "",
      location: lead.location || "",
      remark: lead.remark || "",
      callDate: lead.callDate ? new Date(lead.callDate) : null,
      callBookedLost: lead.callBookedLost || null,
      callStatus: lead.callStatus || null,
      callOutcome: lead.callOutcome || null,
      nadDate: lead.nadDate ? new Date(lead.nadDate) : null,
      nadBookedLost: lead.nadBookedLost || null,
    },
  });

   // Function to handle form submission
   const handleFormSubmit = async (values: FormValues) => {
      // Prepare the data to be submitted: only include changed fields
      const changedData: Partial<Lead> = { id: lead.id }; // Always include ID

       (Object.keys(values) as Array<keyof FormValues>).forEach(key => {
          // Handle Date objects: convert to ISO string or null
         if (key === 'callDate' || key === 'nadDate') {
            const formDate = values[key];
            const initialDateStr = lead[key];
            const initialDate = initialDateStr ? new Date(initialDateStr).toISOString().split('T')[0] : null;
            const formDateStr = formDate ? formDate.toISOString().split('T')[0] : null;

             if (formDateStr !== initialDate) {
               changedData[key] = formDate ? formDate.toISOString() : null;
             }
         } else if (values[key] !== form.formState.defaultValues?.[key]) {
             // For non-date fields, check if the value has changed from the initial default value
             // Type assertion needed here because TS struggles with complex generics
             (changedData as any)[key] = values[key] === "" ? null : values[key]; // Send null if empty string
          }
       });

        // Add lastUpdated timestamp
       changedData.lastUpdated = new Date().toISOString();

       console.log("Submitting changed data:", changedData);
       await onSubmit(changedData);
   };


  return (
    <Form {...form}>
      <form onSubmit={form.handleSubmit(handleFormSubmit)} className="space-y-6 p-1">
        {/* Basic Info - Admin Only */}
        {userRole === 'admin' && (
          <div className="grid grid-cols-1 gap-4 md:grid-cols-2">
            <FormField
              control={form.control}
              name="name"
              disabled={!isFieldEditable("name", userRole) || isLoading}
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Name</FormLabel>
                  <FormControl>
                    <Input placeholder="Lead Name" {...field} />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
            <FormField
              control={form.control}
              name="phone"
              disabled={!isFieldEditable("phone", userRole) || isLoading}
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Phone</FormLabel>
                  <FormControl>
                    <Input placeholder="Phone Number" {...field} />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
            <FormField
              control={form.control}
              name="email"
              disabled={!isFieldEditable("email", userRole) || isLoading}
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Email (Optional)</FormLabel>
                  <FormControl>
                    <Input type="email" placeholder="email@example.com" {...field} />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
             <FormField
              control={form.control}
              name="company"
              disabled={!isFieldEditable("company", userRole) || isLoading}
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Company (Optional)</FormLabel>
                  <FormControl>
                    <Input placeholder="Company Name" {...field} />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
             <FormField
              control={form.control}
              name="source"
              disabled={!isFieldEditable("source", userRole) || isLoading}
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Source</FormLabel>
                  <FormControl>
                    <Input placeholder="Lead Source" {...field} />
                  </FormControl>
                   <FormDescription>e.g., Website, Referral, Upload</FormDescription>
                  <FormMessage />
                </FormItem>
              )}
            />
             <FormField
              control={form.control}
              name="assignedTo"
              disabled={!isFieldEditable("assignedTo", userRole) || isLoading}
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Assigned To</FormLabel>
                   {/* Replace with a Select dropdown populated with employees if needed */}
                  <FormControl>
                    <Input placeholder="Employee ID or Name" {...field} />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
          </div>
        )}

        {/* Call Details - Editable by Both */}
         <div className="grid grid-cols-1 gap-4 md:grid-cols-2">
           <FormField
              control={form.control}
              name="callDate"
              disabled={!isFieldEditable("callDate", userRole) || isLoading}
              render={({ field }) => (
                <FormItem className="flex flex-col">
                  <FormLabel>Call Date</FormLabel>
                  <Popover>
                    <PopoverTrigger asChild>
                      <FormControl>
                        <Button
                          variant={"outline"}
                          className={cn(
                            "w-full pl-3 text-left font-normal",
                            !field.value && "text-muted-foreground"
                          )}
                        >
                          {field.value ? (
                            format(field.value, "PPP")
                          ) : (
                            <span>Pick a date</span>
                          )}
                          <CalendarIcon className="ml-auto h-4 w-4 opacity-50" />
                        </Button>
                      </FormControl>
                    </PopoverTrigger>
                    <PopoverContent className="w-auto p-0" align="start">
                      <Calendar
                        mode="single"
                        selected={field.value ?? undefined} // Pass undefined if null
                        onSelect={(date) => field.onChange(date ?? null)} // Update form state with Date or null
                        disabled={(date) =>
                          date > new Date() || date < new Date("1900-01-01")
                        }
                        initialFocus
                      />
                    </PopoverContent>
                  </Popover>
                   <FormMessage />
                </FormItem>
              )}
            />
            <FormField
              control={form.control}
              name="callBookedLost"
               disabled={!isFieldEditable("callBookedLost", userRole) || isLoading}
              render={({ field }) => (
                <FormItem className="space-y-3">
                   <FormLabel>Call Status (Booked/Lost)</FormLabel>
                   <FormControl>
                    <RadioGroup
                      onValueChange={field.onChange}
                      value={field.value ?? ""} // Use empty string if null
                      className="flex space-x-4 pt-2"
                    >
                      <FormItem className="flex items-center space-x-2 space-y-0">
                        <FormControl>
                          <RadioGroupItem value="Booked" />
                        </FormControl>
                        <FormLabel className="font-normal">Booked</FormLabel>
                      </FormItem>
                      <FormItem className="flex items-center space-x-2 space-y-0">
                        <FormControl>
                          <RadioGroupItem value="Lost" />
                        </FormControl>
                        <FormLabel className="font-normal">Lost</FormLabel>
                      </FormItem>
                       <FormItem className="flex items-center space-x-2 space-y-0">
                        <FormControl>
                           {/* Use a button or visually distinct element for clearing */}
                           <Button type="button" variant="ghost" size="sm" onClick={() => field.onChange(null)} disabled={isLoading}>Clear</Button>
                        </FormControl>
                      </FormItem>
                    </RadioGroup>
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />

            <FormField
              control={form.control}
              name="callStatus"
              disabled={!isFieldEditable("callStatus", userRole) || isLoading}
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Call Status (Connection)</FormLabel>
                  <Select onValueChange={field.onChange} value={field.value ?? ""}>
                    <FormControl>
                      <SelectTrigger>
                        <SelectValue placeholder="Select connection status" />
                      </SelectTrigger>
                    </FormControl>
                    <SelectContent>
                       <SelectItem value="">-- Clear --</SelectItem>
                       {callStatuses.filter(s => s !== null).map(status => (
                           <SelectItem key={status} value={status!}>
                           {status}
                         </SelectItem>
                       ))}
                    </SelectContent>
                  </Select>
                  <FormMessage />
                </FormItem>
              )}
            />
             <FormField
              control={form.control}
              name="callOutcome"
              disabled={!isFieldEditable("callOutcome", userRole) || isLoading}
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Call Outcome</FormLabel>
                   <Select onValueChange={field.onChange} value={field.value ?? ""}>
                    <FormControl>
                      <SelectTrigger>
                        <SelectValue placeholder="Select call outcome" />
                      </SelectTrigger>
                    </FormControl>
                     <SelectContent>
                       <SelectItem value="">-- Clear --</SelectItem>
                       {callOutcomes.filter(o => o !== null).map(outcome => (
                         <SelectItem key={outcome} value={outcome!}>
                           {outcome}
                         </SelectItem>
                       ))}
                     </SelectContent>
                  </Select>
                  <FormMessage />
                </FormItem>
              )}
            />
        </div>

        {/* NAD and Location - Editable by Both */}
        <div className="grid grid-cols-1 gap-4 md:grid-cols-2">
           <FormField
              control={form.control}
              name="nadDate"
              disabled={!isFieldEditable("nadDate", userRole) || isLoading}
              render={({ field }) => (
                <FormItem className="flex flex-col">
                  <FormLabel>Next Action Date (NAD)</FormLabel>
                  <Popover>
                    <PopoverTrigger asChild>
                      <FormControl>
                        <Button
                          variant={"outline"}
                           className={cn(
                            "w-full pl-3 text-left font-normal",
                            !field.value && "text-muted-foreground"
                          )}
                        >
                          {field.value ? (
                            format(field.value, "PPP")
                          ) : (
                            <span>Pick a date</span>
                          )}
                          <CalendarIcon className="ml-auto h-4 w-4 opacity-50" />
                        </Button>
                      </FormControl>
                    </PopoverTrigger>
                    <PopoverContent className="w-auto p-0" align="start">
                       <Calendar
                        mode="single"
                        selected={field.value ?? undefined}
                        onSelect={(date) => field.onChange(date ?? null)}
                        initialFocus
                      />
                    </PopoverContent>
                  </Popover>
                  <FormMessage />
                </FormItem>
              )}
            />
             <FormField
              control={form.control}
              name="nadBookedLost"
               disabled={!isFieldEditable("nadBookedLost", userRole) || isLoading}
              render={({ field }) => (
                <FormItem className="space-y-3">
                   <FormLabel>NAD Status (Booked/Lost)</FormLabel>
                   <FormControl>
                     <RadioGroup
                      onValueChange={field.onChange}
                       value={field.value ?? ""} // Use empty string if null
                      className="flex space-x-4 pt-2"
                    >
                       <FormItem className="flex items-center space-x-2 space-y-0">
                        <FormControl>
                          <RadioGroupItem value="Booked" />
                        </FormControl>
                        <FormLabel className="font-normal">Booked</FormLabel>
                      </FormItem>
                      <FormItem className="flex items-center space-x-2 space-y-0">
                        <FormControl>
                          <RadioGroupItem value="Lost" />
                        </FormControl>
                        <FormLabel className="font-normal">Lost</FormLabel>
                      </FormItem>
                       <FormItem className="flex items-center space-x-2 space-y-0">
                        <FormControl>
                          <Button type="button" variant="ghost" size="sm" onClick={() => field.onChange(null)} disabled={isLoading}>Clear</Button>
                        </FormControl>
                      </FormItem>
                    </RadioGroup>
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
              <FormField
                control={form.control}
                name="location"
                disabled={!isFieldEditable("location", userRole) || isLoading}
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Location</FormLabel>
                    <FormControl>
                      <Input placeholder="Lead Location" {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
        </div>

        {/* Remarks - Editable by Both */}
        <FormField
          control={form.control}
          name="remark"
          disabled={!isFieldEditable("remark", userRole) || isLoading}
          render={({ field }) => (
            <FormItem>
              <FormLabel>Remark</FormLabel>
              <FormControl>
                <Textarea
                  placeholder="Add remarks here..."
                  className="resize-y min-h-[100px]"
                  {...field}
                />
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        />

        <div className="flex justify-end space-x-2 pt-4">
           <Button type="button" variant="outline" onClick={onCancel} disabled={isLoading}>
              Cancel
           </Button>
          <Button type="submit" disabled={isLoading || !form.formState.isDirty}>
            {isLoading ? "Saving..." : "Save Changes"}
          </Button>
        </div>
      </form>
    </Form>
  );
}
