"use client";

import type { ColumnDef } from "@tanstack/react-table";
import type { Lead, CallStatus, CallOutcome } from "@/types/lead";
import { Badge } from "@/components/ui/badge";
import { Checkbox } from "@/components/ui/checkbox";
import { Button } from "@/components/ui/button";
import { ArrowUpDown, MoreHorizontal, Edit, Trash2 } from "lucide-react";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { format } from 'date-fns'; // For date formatting

// Helper function to format date strings or return empty if null/invalid
const formatDate = (dateString: string | null | undefined) => {
  if (!dateString) return "";
  try {
    return format(new Date(dateString), 'PP'); // Format like 'Sep 15, 2024'
  } catch (e) {
    return "Invalid Date";
  }
};

// Define available options for filtering/editing
export const callStatuses: CallStatus[] = ["Connected", "Not Connected"];
export const callOutcomes: CallOutcome[] = ["Prospect", "Call Back", "Not Interested", "Ringing", "Call Disconnected", "Won by GRC"];


export const getColumns = (
  isEditable: (field: keyof Lead) => boolean,
  onEdit: (lead: Lead) => void,
  onDelete?: (leadId: number | string) => void, // Optional delete handler for Admin
): ColumnDef<Lead>[] => {

  const columns: ColumnDef<Lead>[] = [
    {
      id: "select",
      header: ({ table }) => (
        <Checkbox
          checked={
            table.getIsAllPageRowsSelected() ||
            (table.getIsSomePageRowsSelected() && "indeterminate")
          }
          onCheckedChange={(value) => table.toggleAllPageRowsSelected(!!value)}
          aria-label="Select all"
          className="translate-y-[2px]"
        />
      ),
      cell: ({ row }) => (
        <Checkbox
          checked={row.getIsSelected()}
          onCheckedChange={(value) => row.toggleSelected(!!value)}
          aria-label="Select row"
          className="translate-y-[2px]"
        />
      ),
      enableSorting: false,
      enableHiding: false,
    },
    {
      accessorKey: "name",
      header: ({ column }) => (
        <Button
          variant="ghost"
          onClick={() => column.toggleSorting(column.getIsSorted() === "asc")}
        >
          Name
          <ArrowUpDown className="ml-2 h-4 w-4" />
        </Button>
      ),
      cell: ({ row }) => <div className="font-medium">{row.getValue("name")}</div>,
      enableHiding: false, // Usually want name visible
    },
    {
      accessorKey: "phone",
      header: "Phone",
      cell: ({ row }) => <div>{row.getValue("phone")}</div>,
    },
    {
      accessorKey: "company",
      header: "Company",
      cell: ({ row }) => <div>{row.getValue("company") || "-"}</div>,
    },
    {
      accessorKey: "location",
       header: ({ column }) => (
        <Button
          variant="ghost"
          onClick={() => column.toggleSorting(column.getIsSorted() === "asc")}
        >
          Location
          <ArrowUpDown className="ml-2 h-4 w-4" />
        </Button>
      ),
      cell: ({ row }) => <div>{row.getValue("location") || "-"}</div>,
    },
    {
      accessorKey: "assignedTo",
       header: ({ column }) => (
        <Button
          variant="ghost"
          onClick={() => column.toggleSorting(column.getIsSorted() === "asc")}
        >
          Assigned To
          <ArrowUpDown className="ml-2 h-4 w-4" />
        </Button>
      ),
      cell: ({ row }) => <div>{row.getValue("assignedTo") || "Unassigned"}</div>,
      // Hide this column for employees if they only see their own leads
    },
    {
      accessorKey: "callDate",
      header: ({ column }) => (
        <Button
          variant="ghost"
          onClick={() => column.toggleSorting(column.getIsSorted() === "asc")}
        >
          Call Date
          <ArrowUpDown className="ml-2 h-4 w-4" />
        </Button>
      ),
      cell: ({ row }) => (
        <div>
          {formatDate(row.getValue("callDate"))}
           {row.original.callBookedLost && (
             <Badge variant={row.original.callBookedLost === 'Booked' ? 'default' : 'secondary'} className="ml-2">{row.original.callBookedLost}</Badge>
           )}
        </div>
      ),
      sortingFn: 'datetime',
    },
    {
      accessorKey: "callStatus",
      header: "Call Status",
      cell: ({ row }) => {
        const status: CallStatus = row.getValue("callStatus");
        if (!status) return "-";
        const variant: "default" | "secondary" = status === "Connected" ? "default" : "secondary";
        return <Badge variant={variant}>{status}</Badge>;
      },
      filterFn: (row, id, value) => {
        return value.includes(row.getValue(id));
      }
    },
    {
      accessorKey: "callOutcome",
      header: "Call Outcome",
      cell: ({ row }) => {
        const outcome: CallOutcome = row.getValue("callOutcome");
         if (!outcome) return "-";
         let variant: "default" | "secondary" | "destructive" | "outline" = "secondary";
         if (outcome === "Prospect") variant = "default";
         if (outcome === "Won by GRC") variant = "default"; // Or a success variant if available
         if (outcome === "Not Interested" || outcome === "Call Disconnected") variant = "destructive";
         if (outcome === "Call Back") variant = "outline"; // Example

        return <Badge variant={variant} className="whitespace-nowrap">{outcome}</Badge>;
      },
      filterFn: (row, id, value) => {
        return value.includes(row.getValue(id));
      }
    },
    {
      accessorKey: "nadDate",
       header: ({ column }) => (
        <Button
          variant="ghost"
          onClick={() => column.toggleSorting(column.getIsSorted() === "asc")}
        >
          NAD
          <ArrowUpDown className="ml-2 h-4 w-4" />
        </Button>
      ),
      cell: ({ row }) => (
         <div>
          {formatDate(row.getValue("nadDate"))}
           {row.original.nadBookedLost && (
             <Badge variant={row.original.nadBookedLost === 'Booked' ? 'default' : 'secondary'} className="ml-2">{row.original.nadBookedLost}</Badge>
           )}
        </div>
      ),
      sortingFn: 'datetime',
    },
    {
      accessorKey: "remark",
      header: "Remark",
      cell: ({ row }) => (
        <div className="truncate max-w-xs">{row.getValue("remark") || "-"}</div>
      ),
    },
     {
      accessorKey: "lastUpdated",
      header: ({ column }) => (
        <Button
          variant="ghost"
          onClick={() => column.toggleSorting(column.getIsSorted() === "asc")}
        >
          Last Updated
          <ArrowUpDown className="ml-2 h-4 w-4" />
        </Button>
      ),
      cell: ({ row }) => <div>{formatDate(row.getValue("lastUpdated"))}</div>,
       sortingFn: 'datetime',
       enableHiding: true, // Might hide by default
    },
    {
      id: "actions",
      cell: ({ row }) => {
        const lead = row.original;
        const canEditAny = Object.keys(lead).some(key => isEditable(key as keyof Lead));

        return (
          <DropdownMenu>
            <DropdownMenuTrigger asChild>
              <Button variant="ghost" className="h-8 w-8 p-0">
                <span className="sr-only">Open menu</span>
                <MoreHorizontal className="h-4 w-4" />
              </Button>
            </DropdownMenuTrigger>
            <DropdownMenuContent align="end">
              <DropdownMenuLabel>Actions</DropdownMenuLabel>
               <DropdownMenuItem onClick={() => navigator.clipboard.writeText(lead.phone)}>
                Copy Phone
              </DropdownMenuItem>
              {canEditAny && (
                 <DropdownMenuItem onClick={() => onEdit(lead)}>
                   <Edit className="mr-2 h-4 w-4" />
                   Edit Lead
                 </DropdownMenuItem>
              )}
              {onDelete && ( // Conditionally render delete for admin
                <>
                  <DropdownMenuSeparator />
                  <DropdownMenuItem
                     className="text-destructive focus:text-destructive focus:bg-destructive/10"
                     onClick={() => onDelete(lead.id)}
                   >
                     <Trash2 className="mr-2 h-4 w-4" />
                     Delete Lead
                   </DropdownMenuItem>
                 </>
              )}
            </DropdownMenuContent>
          </DropdownMenu>
        );
      },
      enableSorting: false,
      enableHiding: false,
    },
  ];

  // Filter columns based on editability (useful for employee view)
  // This logic might be better handled by controlling which columns are passed initially
  // based on role, rather than filtering here. Keeping it simple for now.
  // const visibleColumns = columns.filter(col => {
  //    if (col.id === 'actions' || col.id === 'select') return true;
  //    const key = col.accessorKey as keyof Lead | undefined;
  //    return key ? isEditable(key) : true; // Show if editable or not an accessor column
  // });
  // return visibleColumns;

  return columns;
};
