"use client";

import * as React from "react";
import {
  ColumnDef,
  ColumnFiltersState,
  SortingState,
  VisibilityState,
  flexRender,
  getCoreRowModel,
  getFilteredRowModel,
  getPaginationRowModel,
  getSortedRowModel,
  useReactTable,
} from "@tanstack/react-table";

import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import {
  DropdownMenu,
  DropdownMenuCheckboxItem,
  DropdownMenuContent,
  DropdownMenuTrigger,
  DropdownMenuLabel,
  DropdownMenuSeparator
} from "@/components/ui/dropdown-menu";
import { ChevronDown, Filter as FilterIcon } from "lucide-react";
import type { Lead, CallStatus, CallOutcome } from "@/types/lead";
import { callStatuses, callOutcomes } from './lead-table-columns'; // Import options

interface DataTableProps<TData extends Lead, TValue> {
  columns: ColumnDef<TData, TValue>[];
  data: TData[];
  onEdit: (lead: TData) => void; // Callback for editing
  onDelete?: (leadId: number | string) => void; // Optional callback for deleting (Admin)
  userRole: 'admin' | 'employee'; // Pass user role
  availableEmployees?: string[]; // For admin filtering
}

export function LeadDataTable<TData extends Lead, TValue>({
  columns,
  data,
  onEdit,
  onDelete,
  userRole,
  availableEmployees = [],
}: DataTableProps<TData, TValue>) {
  const [sorting, setSorting] = React.useState<SortingState>([]);
  const [columnFilters, setColumnFilters] = React.useState<ColumnFiltersState>([]);
  const [columnVisibility, setColumnVisibility] = React.useState<VisibilityState>({
    // Hide columns by default if needed, e.g., less relevant ones
     lastUpdated: false,
     company: userRole === 'employee', // Hide company for employee? Adjust as needed
     source: true, // Example: show source initially
     email: true, // Example: show email initially
     // Ensure 'assignedTo' visibility is handled correctly based on role if needed
     assignedTo: userRole === 'employee' ? false : true, // Hide for employee if they only see their own
  });
  const [rowSelection, setRowSelection] = React.useState({});
  const [globalFilter, setGlobalFilter] = React.useState('');


  const table = useReactTable({
    data,
    columns,
    state: {
      sorting,
      columnFilters,
      columnVisibility,
      rowSelection,
      globalFilter,
    },
    onSortingChange: setSorting,
    onColumnFiltersChange: setColumnFilters,
    onColumnVisibilityChange: setColumnVisibility,
    onRowSelectionChange: setRowSelection,
    onGlobalFilterChange: setGlobalFilter,
    getCoreRowModel: getCoreRowModel(),
    getSortedRowModel: getSortedRowModel(),
    getFilteredRowModel: getFilteredRowModel(),
    getPaginationRowModel: getPaginationRowModel(),
    globalFilterFn: 'includesString', // Basic global search
  });

   // Filter options based on role
   const getFilterOptions = (columnId: string): string[] => {
    switch (columnId) {
      case 'callStatus':
        return callStatuses.filter(s => s !== null) as string[];
      case 'callOutcome':
        return callOutcomes.filter(o => o !== null) as string[];
      case 'assignedTo':
        return userRole === 'admin' ? availableEmployees : []; // Only admin can filter by employee
      default:
        return [];
    }
  };

  return (
    <div className="w-full space-y-4">
      {/* Filters and Actions Bar */}
      <div className="flex items-center justify-between gap-2 py-4">
         <Input
          placeholder="Search all columns..."
          value={globalFilter ?? ''}
          onChange={(event) => setGlobalFilter(event.target.value)}
          className="max-w-sm h-9" // Smaller height
        />

        <div className="flex items-center gap-2">
         {/* Column Visibility Dropdown */}
         <DropdownMenu>
            <DropdownMenuTrigger asChild>
              <Button variant="outline" size="sm" className="ml-auto">
                Columns <ChevronDown className="ml-2 h-4 w-4" />
              </Button>
            </DropdownMenuTrigger>
            <DropdownMenuContent align="end">
              <DropdownMenuLabel>Toggle Columns</DropdownMenuLabel>
              <DropdownMenuSeparator />
              {table
                .getAllColumns()
                .filter((column) => column.getCanHide())
                .map((column) => {
                  return (
                    <DropdownMenuCheckboxItem
                      key={column.id}
                      className="capitalize"
                      checked={column.getIsVisible()}
                      onCheckedChange={(value) =>
                        column.toggleVisibility(!!value)
                      }
                    >
                      {column.id.replace(/([A-Z])/g, ' $1').trim()} {/* Add spaces */}
                    </DropdownMenuCheckboxItem>
                  );
                })}
            </DropdownMenuContent>
          </DropdownMenu>

            {/* Filter Dropdown */}
           <DropdownMenu>
             <DropdownMenuTrigger asChild>
               <Button variant="outline" size="sm">
                 <FilterIcon className="mr-2 h-4 w-4" /> Filters
               </Button>
             </DropdownMenuTrigger>
             <DropdownMenuContent align="end" className="w-[250px]">
               <DropdownMenuLabel>Filter by</DropdownMenuLabel>
               <DropdownMenuSeparator />
                 {/* Call Status Filter */}
                 {table.getColumn('callStatus') && (
                    <>
                     <DropdownMenuLabel className="text-xs font-semibold px-2 pt-2">Call Status</DropdownMenuLabel>
                     {getFilterOptions('callStatus').map((status) => (
                       <DropdownMenuCheckboxItem
                          key={status}
                          checked={(table.getColumn('callStatus')?.getFilterValue() as string[] | undefined)?.includes(status) ?? false}
                          onCheckedChange={(checked) => {
                            const currentFilter = (table.getColumn('callStatus')?.getFilterValue() as string[] | undefined) ?? [];
                            const newFilter = checked
                              ? [...currentFilter, status]
                              : currentFilter.filter(s => s !== status);
                             table.getColumn('callStatus')?.setFilterValue(newFilter.length ? newFilter : undefined);
                          }}
                       >
                         {status}
                       </DropdownMenuCheckboxItem>
                     ))}
                      <DropdownMenuSeparator />
                    </>
                 )}
                  {/* Call Outcome Filter */}
                 {table.getColumn('callOutcome') && (
                    <>
                     <DropdownMenuLabel className="text-xs font-semibold px-2 pt-2">Call Outcome</DropdownMenuLabel>
                     {getFilterOptions('callOutcome').map((outcome) => (
                       <DropdownMenuCheckboxItem
                          key={outcome}
                          checked={(table.getColumn('callOutcome')?.getFilterValue() as string[] | undefined)?.includes(outcome) ?? false}
                           onCheckedChange={(checked) => {
                            const currentFilter = (table.getColumn('callOutcome')?.getFilterValue() as string[] | undefined) ?? [];
                            const newFilter = checked
                              ? [...currentFilter, outcome]
                              : currentFilter.filter(s => s !== outcome);
                             table.getColumn('callOutcome')?.setFilterValue(newFilter.length ? newFilter : undefined);
                          }}
                       >
                         {outcome}
                       </DropdownMenuCheckboxItem>
                     ))}
                      <DropdownMenuSeparator />
                    </>
                 )}
                  {/* Assigned To Filter (Admin only) */}
                 {userRole === 'admin' && table.getColumn('assignedTo') && getFilterOptions('assignedTo').length > 0 && (
                    <>
                     <DropdownMenuLabel className="text-xs font-semibold px-2 pt-2">Assigned To</DropdownMenuLabel>
                     {getFilterOptions('assignedTo').map((employee) => (
                       <DropdownMenuCheckboxItem
                          key={employee}
                          checked={(table.getColumn('assignedTo')?.getFilterValue() as string[] | undefined)?.includes(employee) ?? false}
                           onCheckedChange={(checked) => {
                            const currentFilter = (table.getColumn('assignedTo')?.getFilterValue() as string[] | undefined) ?? [];
                            const newFilter = checked
                              ? [...currentFilter, employee]
                              : currentFilter.filter(s => s !== employee);
                             table.getColumn('assignedTo')?.setFilterValue(newFilter.length ? newFilter : undefined);
                          }}
                       >
                         {employee || "Unassigned"}
                       </DropdownMenuCheckboxItem>
                     ))}
                    </>
                 )}
             </DropdownMenuContent>
           </DropdownMenu>
        </div>
      </div>

      {/* Table */}
      <div className="rounded-md border shadow-sm">
        <Table>
          <TableHeader>
            {table.getHeaderGroups().map((headerGroup) => (
              <TableRow key={headerGroup.id}>
                {headerGroup.headers.map((header) => {
                  return (
                    <TableHead key={header.id} className="whitespace-nowrap">
                      {header.isPlaceholder
                        ? null
                        : flexRender(
                            header.column.columnDef.header,
                            header.getContext()
                          )}
                    </TableHead>
                  );
                })}
              </TableRow>
            ))}
          </TableHeader>
          <TableBody>
            {table.getRowModel().rows?.length ? (
              table.getRowModel().rows.map((row) => (
                <TableRow
                  key={row.id}
                  data-state={row.getIsSelected() && "selected"}
                >
                  {row.getVisibleCells().map((cell) => (
                    <TableCell key={cell.id}>
                      {flexRender(
                        cell.column.columnDef.cell,
                        cell.getContext()
                      )}
                    </TableCell>
                  ))}
                </TableRow>
              ))
            ) : (
              <TableRow>
                <TableCell
                  colSpan={columns.length}
                  className="h-24 text-center"
                >
                  No results found.
                </TableCell>
              </TableRow>
            )}
          </TableBody>
        </Table>
      </div>

      {/* Pagination */}
      <div className="flex items-center justify-between space-x-2 py-4">
         <div className="flex-1 text-sm text-muted-foreground">
          {table.getFilteredSelectedRowModel().rows.length} of{" "}
          {table.getFilteredRowModel().rows.length} row(s) selected.
        </div>
        <div className="flex items-center space-x-2">
           <span className="text-sm text-muted-foreground">
             Page {table.getState().pagination.pageIndex + 1} of {table.getPageCount()}
           </span>
          <Button
            variant="outline"
            size="sm"
            onClick={() => table.previousPage()}
            disabled={!table.getCanPreviousPage()}
          >
            Previous
          </Button>
          <Button
            variant="outline"
            size="sm"
            onClick={() => table.nextPage()}
            disabled={!table.getCanNextPage()}
          >
            Next
          </Button>
        </div>
      </div>
    </div>
  );
}
